import { useState } from "react";

export default function ProgressStepper() {
  const [activeStep, setActiveStep] = useState(0);
  const totalSteps = 3;

  const handleNext = () => {
    if (activeStep < totalSteps - 1) {
      setActiveStep((prevActiveStep) => prevActiveStep + 1);
    }
  };

  const handleSkip = () => {
    setActiveStep(totalSteps - 1);
  };

  return (
    <div className="flex flex-col max-w-md mx-auto w-full p-3 bg-white rounded-lg">
      <div className="flex justify-center gap-3 mb-3">
        {Array.from({ length: totalSteps }).map((_, index) => (
          <div
            key={index}
            className={`h-3 rounded-full transition-all duration-300 ${
              index === activeStep ? "bg-[#ed9426] w-8" : "bg-gray-300 w-3"
            }`}
          />
        ))}
      </div>

      <div className="w-full h-0.5 bg-[#ed9426] mb-3" />
      <div className="flex justify-center items-center gap-4">
        <button
          onClick={handleSkip}
          className="text-[#ed9426] font-medium py-2 px-5 rounded-full hover:bg-orange-50 transition-colors"
        >
          Skip
        </button>

        <button
          onClick={handleNext}
          disabled={activeStep >= totalSteps - 1}
          className="bg-[#ed9426] text-white font-medium py-2 px-7 rounded-full hover:bg-[#d17d1f] transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
        >
          Continue
        </button>
      </div>
    </div>
  );
}
